<template>
  <main id="app">
    <div class="container">
      <div class="row">
        <div class="col">

          <form class="form-inline mb-4">
            <div class="form-check form-check-inline">
              <input class="form-check-input" type="radio" id="pc" value="pc" v-model="platform">
              <label class="form-check-label" for="pc">PC</label>
            </div>
            <div class="form-check form-check-inline">
              <input class="form-check-input" type="radio" id="ps4" value="psn" v-model="platform" checked="checked">
              <label class="form-check-label" for="ps4">PS4</label>
            </div>
            <div class="form-check form-check-inline">
              <input class="form-check-input" type="radio" id="xbox" value="xbx" v-model="platform" checked="checked">
              <label class="form-check-label" for="xbox">Xbox</label>
            </div>
            <div class="form-group">
              <label class="mr-sm-2" for="userName">Epic Games username</label>
              <input class="form-control ml-md" id="userName" type="text" @input="isTyping = true" v-model="userName" placeholder="e.g. Ninja" value="username">
            </div>
          </form>

          <transition name="fade">
            <div class="alert alert-info" role="alert" v-if="loading">
              Loading.....
            </div>
          </transition>

          <div v-if="recentMatches">
            <h3>Recent Matches</h3>
            <table v-if="recentMatches" class="table table-hover table-dark">
              <thead>
                <tr>
                  <th scope="col">#</th>
                  <th scope="col">Date</th>
                  <th scope="col">Kills</th>
                  <th scope="col">Score</th>
                </tr>
              </thead>
              <tbody>
                <recent-matches
                  v-for="(match, index) in recentMatches"
                  v-bind:kills="match.kills"
                  v-bind:score="match.score"
                  v-bind:key="index"
                  v-bind:index="index"
                  v-bind:dateCollected="match.dateCollected"
                ></recent-matches>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import Axios from 'axios';
import _ from 'lodash';

export default {
  name: 'app',
  data() {
    return {
      platform: 'psn',
      userName: '',
      isTyping: false,
      loading: false,
      recentMatches: null,
    };
  },
  watch: {
    userName: _.debounce(function typing() {
      this.isTyping = false;
    }, 700),
    isTyping(value) {
      if (!value) {
        this.searchUser(this.userName);
      }
    },
  },
  methods: {
    searchUser() {
      this.loading = true;
      Axios.get(`https://cors-anywhere.herokuapp.com/https://api.fortnitetracker.com/v1/profile/${this.platform}/${this.userName}`, {
        headers: {
          'TRN-Api-Key': 'dfae8ecd-11fc-4691-a8ed-bef6ad8c4364',
        },
      })
        .then((response) => {
          this.loading = false;
          this.userName = response.data.value;
          this.recentMatches = response.data.recentMatches;
          this.userName = response.data.epicUserHandle;
        }, (error) => {
          this.loading = false;
          return error;
        });
    },
  },
};
</script>

<style>
  #app {
    font-family: "Source Sans Pro", "Helvetica Neue", Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    margin-top: 60px;
  }

  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>
